import React, {useState, useEffect} from 'react';
import {connect} from 'react-redux';
import {Card, Divider, Menu, message, Form, Table, Modal, Button, Drawer, Tooltip, Select} from "antd";
import ApiService from "../../../../../../../services/api.service";
import Flex from "../../../../../../../components/shared-components/Flex";
import {
	EditOutlined,
	EyeOutlined,
	FilterOutlined,
	PlusCircleOutlined,
	ReloadOutlined,
	SettingOutlined
} from "@ant-design/icons";
import {Link} from "react-router-dom";
import Utils, {GetPath} from "../../../../../../../utils";
import LayoutPermission from "components/permission-components/layout.permission";
import EllipsisDropdown from "components/shared-components/EllipsisDropdown";
import moment from "moment";
import DisplaySystemForm from "./display-system.form";
import PreloaderText from "../../../../../../../components/animated-components/preloader-text";
import {ON_DISPLAY_SYS_PUSH} from "../../../../../../../constants/socket.event";
import {SocketSend} from "../../../../../../../redux/actions/websocket.action";

function mapStateToProps(state) {
	return {};
}

/**
 *
 * @param {Object} props
 * @param {String} props.type
 * @returns {JSX.Element}
 * @constructor
 */
function GeneralOverview(props) {
	let {type, SocketSend} = props
	const defaultColumns = [
		{
			visible:true,
			key: "date",
			dataIndex: "date",
			title: "Date",
			render: (_) => {
				if (moment(_).isValid()) {
					return moment(_).format("YYYY/MM/DD")
				}
				return "-"
			}
		},
		{
			visible:true,
			key: "type",
			dataIndex: "type",
			title: "Type",
		},
		{
			visible:true,
			key: "airline",
			dataIndex: "airline",
			title: "Airlines",
			render: (_) => {
				return _?.data?.name ?? "-"
			}
		},
		{
			visible:true,
			key: "flight_no",
			dataIndex: "flight_no",
			title: "Flight No",
		},
		{
			visible:true,
			key: "origin",
			dataIndex: "origin",
			title: "From / To",
			render: (_) => {
				return _?.data?.name ?? "-"
			}
		},
		{
			visible:true,
			key: "schedule_time",
			dataIndex: "schedule_time",
			title: "Schedule Time",
			render: (_) => _ !== "" ? _ : "-"
		},
		{
			visible:true,
			key: "actual_time",
			dataIndex: "actual_time",
			title: "Actual Time",
			render: (_) => _ !== "" ? _ : "-"
		},
		{
			visible:true,
			key: "status",
			dataIndex: "status",
			title: "Status"
		},
		{
			visible:true,
			key: "action",
			dataIndex:"id",
			width: 80,
			render: (_, val, index) => {
				return (
					<div className={'text-right'}>
						<EllipsisDropdown menu={
							<Menu>
								<LayoutPermission
									extra={<></>}
									permission={['public', 'admin']}>
									<Menu.Item onClick={() => onSelected(val)}>
										<Flex alignItems="center">
											<EditOutlined/>
											<span className="ml-2">Edit</span>
										</Flex>
									</Menu.Item>
								</LayoutPermission>
								
								<LayoutPermission
									extra={<></>}
									permission={['admin']}>
									<Divider className={'my-0'}/>
									<Menu.Item onClick={()=> onDelete(_)}>
										<Flex alignItems="center">
											<PlusCircleOutlined/>
											<span className="ml-2">Delete</span>
										</Flex>
									</Menu.Item>
								</LayoutPermission>
							</Menu>
						}/>
					</div>
				)
			}
		},
	]
	
	function onDelete(id){
		
		new ApiService({
			url:`/api/v1/flight/${id}`
		}).delete()
			.then((response)=> {
				if(response?.success){
					message.success(response?.message ?? "")
					_caller()
				}else{
					message.info(response?.message)
				}
			})
			.catch((err)=> {
				message.error(err?.message ?? "Some Error")
			})
	}
	const [form] = Form.useForm()
	const [visible, setVisible] = useState(false)
	const [selected, setSelected] = useState(null)
  const [visibleDrawer,setVisibleDrawer] = useState(false)
	const [loadingSubmit, setLoadingSubmit] = useState(false)
	const [loading, setLoading] = useState(false)
	const [params, setParams] = useState({
		page: 1,
		limit: 10
	})
	const [pagination, setPagination] = useState({
		page: 1,
		limit: 10
	})
	const [data, setData] = useState([])
	
	
	function _caller(){
		setLoading(true)
		new ApiService({
			url: `/api/v1/flight/${type}`,
			config: {
				params: {
					...params
				}
			}
		}).get()
			.then((response) => {
				
				setPagination({
					...pagination,
					...response?.pagination
				})
				setData([...response?.data])
				setLoading(false)
			})
			.catch((err) => {
				setData([])
				setLoading(false)
			})
	}
	useEffect(() => {
		if (type) {
			_caller()
		}
	}, [type])
	
	const onSelected = (_) => {
		setSelected(_)
		setVisible(true)
	}
	const onCancel = () => {
		form.resetFields()
		setVisible(false)
	}
	const onFinish = () => {
		setLoadingSubmit(true)
		message.loading({
			key: "create-display-system",
			content: <PreloaderText text={'Loading'} dots={3} speed={300}/>,
			duration: 5
		})
		form.validateFields()
			.then((value) => {
				let fields = {}
				Object.entries(value).map(([key, value]) => {
					switch (key) {
						case "date":
							Reflect.set(fields, key, moment(value).format("YYYY-MM-DD"))
							break;
						case "schedule_time":
						case "actual_time":
							if (value) {
								Reflect.set(fields, key, moment(value).format("HH:mm"))
							} else {
								Reflect.set(fields, key, null)
							}
							break;
						default:
							Reflect.set(fields, key, value)
					}
				})
				
				if (fields && typeof (fields) === 'object' && Object.keys(fields).length > 0) {
					new ApiService({
						url: `/api/v1/flight/${selected?.id}`,
						body: fields,
					}).patch()
						.then((response) => {
							if (response?.success) {
								message.success({
									key: "create-display-system",
									content: response?.message ?? "Successfully! save to database",
									duration: 5
								})
								SocketSend(JSON.stringify({
										from: Utils.getUserId,
										to: fields?.type,
										event: ON_DISPLAY_SYS_PUSH,
										type: "update_event",
										room: fields?.type,
										message: ON_DISPLAY_SYS_PUSH,
										data: {
											...response?.data,
											id: selected?.id
										}
									}
								))
							} else {
								message.error({
									key: "create-display-system",
									content: response?.message ?? "Error: fail to database",
									duration: 5
								})
							}
							
							let indexed = data.findIndex((item) => item?.id === selected?.id)
							if (indexed >= 0) {
								Object.entries(value).map(([key, val]) => {
									switch (key) {
										case "date":
										case "actual_time":
										case "schedule_time":
											break;
										default:
											data[indexed][key] = val
											break
									}
									
								})
								console.log({
									data: data[indexed]
								})
							}
						})
						.catch((err) => {
							message.error({
								key: "create-display-system",
								content: err?.message ?? "Error: fail to database",
								duration: 5
							})
						})
				}
				setLoadingSubmit(false)
				
			})
			.catch((err) => {
				message.error({
					key: "create-display-system",
					content: err?.message ?? "Some Error Field",
					duration: 5
				})
				setLoadingSubmit(false)
			})
	}
  
  const onChangeDrawer = (val)=> {
      setVisibleDrawer(val)
  }
  
	
	useEffect(() => {
		form.resetFields()
	}, [visible])
	
	const [columns, setColumns] = useState(defaultColumns)
    
    const extra = (
      <div className={'text-right d-flex mk-gap-2'}>
	      <Tooltip title={'Reload Data'}>
		      <Button icon={<ReloadOutlined/>} ghost type={'primary'} onClick={_caller} loading={loading} size={'small'}/>
	      </Tooltip>
	      <Tooltip title={'Settings'}>
		      <Button icon={<SettingOutlined/>} type={'primary'} ghost onClick={() => onChangeDrawer(true)} size={'small'}/>
	      </Tooltip>
      </div>
    )
	
	
	const onColumnsChange = (value) => {
		setColumns(() => {
			let filtered = defaultColumns.filter((item) => value.includes(item.key))
			return filtered.map((item)=> ({
				...item,
				visible:true
			}))
		})
	}
	
	return (
		<React.Fragment>
        <Drawer
          title={`${props?.title} Setting`}
          placement={"right"}
          closable
          className={'!mk-capitalize'}
          onClose={()=> onChangeDrawer(false)}
          visible={visibleDrawer}
        >
	        <div className={'w-100'}>
		        <h5>Table Column</h5>
		        <Select onChange={onColumnsChange} mode={'tags'} className={'w-100'} placeholder={'Select Columns Active'}
		                defaultValue={defaultColumns.filter((item)=> item.visible === true).map((item) => item?.key) ?? []}>
			        {
				        defaultColumns.map((item) => (
					        <Select.Option value={item.key} key={item.key}>{`${item.key}`.replace(/_/g, ' ')}</Select.Option>
				        ))
			        }
		        </Select>
		        
		        <Divider/>
	        </div>
        </Drawer>
			<Modal
				visible={visible}
				onCancel={onCancel}
				onOk={onFinish}
				closable={false}
			>
				<DisplaySystemForm
					form={form}
					initialValue={{...selected}}
				/>
			</Modal>
			<Card bordered={false} title={props?.title ?? null} extra={extra}>
				<Table
					rowKey={"id"}
					loading={loading}
					dataSource={data}
					columns={columns.filter((item)=> item.visible === true)}
				/>
			</Card>
		</React.Fragment>
	);
}

export default connect(
	mapStateToProps, {SocketSend}
)(GeneralOverview);