import React, {useEffect, useState} from 'react'
import PropTypes from 'prop-types'
import {connect} from 'react-redux'
import {Link, useHistory} from "react-router-dom";
import {Button, Card, Col, Empty, Row, Tag} from "antd";
import HeroComponents from "components/layout-components/CustomrPageHeaderAlt";
import CustomTab from "components/layout-components/custom.tab";
import GeneralOverview from "../components/general.overview";
import Container from "components/layout-components/container";
import ApiService from "../../../../../../../services/api.service";
import ScreenInView from "../../../../../../../components/layout-components/screen-in-view";
import ButtonPermission from "../../../../../../../components/permission-components/button.permission";
import {PlusOutlined} from "@ant-design/icons";
import {SearchLikeArr} from "../../../../../../../utils";
import WithoutGeneralTableDisplay from "../components/without-general.table.display";


/**
 *
 * @param {Object} props
 * @returns {JSX.Element}
 */
function Overview(props) {
	const history = useHistory()
	
	const [data, setData] = useState([])
	const [loading, setLoading] = useState(false)
	const [params, setParams] = useState({
		page: 1,
		limit: 20
	})
	
	useEffect(() => {
		setLoading(true)
		new ApiService({
			url: `/api/v1/master-data/airline-type`,

			config: {
				params: {
					...params,
				}
			}
		}).get()
			.then((response) => {
				setLoading(false)
				setData([...response?.data])
			})
			.catch((err) => {
				setLoading(false)
				setData([])
			})
	}, [params])
	
	let ARR = SearchLikeArr(data, ['identifier'], 'ARR')
	let DEPA = SearchLikeArr(data, ['identifier'], 'DEPA')
	return (
		<>
			<HeroComponents
				loading={false}
				name={"Overview"}
				containerType={'antd'}
				positionName={['Display ', 'System']}
				thumbnail={null}
				extra={<>
					<ButtonPermission
						permission={['public','admin']}
						theme={{
							type:"disabled",
							property:{
								message:"Access Denied"
							}
						}}
						config={{
							onClick: function(){
								history.push('/dashboard/display/system/create')
							},
							ghost:true,
							icon:<PlusOutlined/>,
							// icon: PlusOutlined,
							type:"primary",
							size:"small"
						}}>
						Add New
					</ButtonPermission>
				</>}
			/>
			
			<Container>
				{
					loading ? "Loading...." : (
						<CustomTab
							defaultTab={"all"}
							tab={data.length > 0 ?
								[
									{
										key: "all",
										title: "All",
										render: ({onChange}) => {
											return (
												<React.Fragment>
													<ScreenInView style={{minHeight: '300px'}}>
														{({isVisible}) => {
															return isVisible && (
																<div key={`display-setting-overview-all`}>
																	<GeneralOverview type={'all' ?? null}
																	                 title={"All" ?? "-"}/>
																</div>
															)
														}}
													</ScreenInView>

												</React.Fragment>
											)
										}
									},
									...[
										...SearchLikeArr(data, ['identifier'], 'arr'),
										...SearchLikeArr(data, ['identifier'], 'depart'),
									].sort((a, b) => -1).map((child) => ({
										key: child?.identifier,
										title: child?.data?.name,
										render: function () {
											return (
												<ScreenInView style={{minHeight: '300px'}}>
													{({isVisible}) => {
														
														return isVisible && (
															<GeneralOverview type={child?.identifier ?? null}/>
														)
													}}
												</ScreenInView>
											)
										}
									})),
									
									...data?.filter((child)=> {
										if(SearchLikeArr([child], ['identifier'], 'arr').length === 0 && SearchLikeArr([child], ['identifier'], 'depart').length === 0) return child
										return null
									}).sort((a, b) => -1).map((child) => ({
										key: child?.identifier,
										title: child?.data?.name,
										render: function () {
											return (
												<ScreenInView style={{minHeight: '300px'}}>
													{({isVisible}) => {
														return isVisible && (
															<WithoutGeneralTableDisplay type={child?.data?.type ?? child?.identifier ?? null}/>
														)
													}}
												</ScreenInView>
											)
										}
									})),
								]
								: []}
						/>
					)
				}
			
			</Container>
		</>
	)
}

Overview.propTypes = {}
Overview.defaultProps = {}
export default connect(({MasterData}) => {
	let {airlines_type} = MasterData
	return {
		type: airlines_type?.list
	}
}, {})(React.memo(Overview))
