import React, { useState,useEffect } from 'react';
import { connect } from 'react-redux';
import HeroComponents from "components/layout-components/CustomrPageHeaderAlt";
import ButtonPermission from "components/permission-components/button.permission";
import ContainerTailwind from "components/layout-components/ContainerTailwind";
import {Button, Card, Form, message, Table, Tooltip} from "antd";
import {DeleteOutlined, EditOutlined, EyeOutlined, FilterOutlined} from "@ant-design/icons";
import { useHistory } from 'react-router-dom'
import ApiService from "services/api.service";
import moment from "moment";


function mapStateToProps(state) {
	return {
	
	};
}

function WithoutGeneralTableDisplay({type, ...props})  {
	const history = useHistory()
	const [loading,setLoading]= useState(true)
	const [ params, setParams]= useState({
		page:1,
		limit:20
	})
	const [ pagination, setPagination ] = useState({
		page:1,
		limit:10
	})
	const [ data, setData] = useState([])
	const defaultColumns = [
		{
			visible:true,
			key:"no",
			title:"No",
			dataIndex: "id",
			width: 70,
			render: (_, val, index) => ((index + 1) + (params?.limit * (params?.page - 1)))
		},
		{
			visible:true,
			key:"display_name",
			dataIndex: ['data', 'name'],
			title: "Display Name",
			render: (_) => _ ?? "-"
		},
		{
			visible:true,
			key:"Airlines",
			dataIndex: ['data', 'airline','name'],
			title: "Airline",
			render: (_) => _ ?? "-"
		},
		{
			visible:true,
			key:"flight_no",
			dataIndex: ['data', 'flight_no'],
			title: "Flight No",
			render: (_) => _ ?? "-"
		},
		{
			visible:true,
			key:"to-or-from",
			dataIndex: ['data', 'city','name'],
			title: "To / From",
			render: (_) => _ ?? "-"
		},
		{
			visible:true,
			key:"start_time",
			dataIndex: ['data', 'start_time'],
			title: "Start Time",
			
			render: (_) => {
				if(moment(_).isValid()){
					return moment(_).format("HH:mm")
				}
				return _
			}
		},
		{
			visible:true,
			key:"end_time",
			dataIndex: ['data', 'end_time'],
			title: "End Time",
			render: (_) => {
				if(moment(_).isValid()){
					return moment(_).format("HH:mm")
				}
				return _
			}
		},
		{
			visible:true,
			key:"status_id",
			dataIndex: ['data', 'status'],
			title: "Status",
			render: (_) => _ ?? "-"
		},
		{
			visible:true,
			key: 'action',
			width: 120,
			dataIndex: 'id',
			render: (_, val, index) => {
				return (
					<div className={'text-right d-flex !mk-gap-2'}>
						{/*<Button size={'small'} type={'primary'} ghost icon={<EditOutlined/>}*/}
						{/*        onClick={() => handleClickEdit(val, index)}/>*/}
						<Button
							size={'small'}
							type={'primary'}
							danger
							ghost
							onClick={() => {
								handleDeleteRow(_, index)
							}}
							loading={loadings[index] ?? false}
							icon={<DeleteOutlined/>}/>
					</div>
				)
			}
		},
	]
	
	const [loadings, setLoadings] = useState([])
	const [visibleDrawer, setVisibleDrawer] = useState(false)
	const [visible, setVisible] = useState(false)
	const [selected, setSelected] = useState(null)
	const [form] = Form.useForm();
	
	const enterLoading = (index, value = true) => {
		setLoadings((prevLoadings) => {
			prevLoadings[index] = value;
			return prevLoadings ?? []
		});
	};
	
	
	function handleDeleteRow(_, index) {
		enterLoading(index, true)
		new ApiService({
			url: `/api/v1/master-data/${_}`,
			mode:"express"
		}).delete()
			.then((response) => {
				if (!response?.error) {
					if (data && typeof (data[index]) !== 'undefined') {
						data[index].isDeleted = true
						setData([...data])
						enterLoading(index, false)
					}
					message.success(response?.message ?? "Successfully deleted")
					setTimeout(() => {
						setData([...data.filter((item) => item.id !== _)])
						setLoadings([])
					}, 2000)
				} else {
					enterLoading(index, false)
					setLoadings([])
					message.error(response?.message ?? "Error: delete by id " + _)
				}
				
			})
			.catch((err) => {
				enterLoading(index, false)
				setLoadings([])
			})
	}
	
	function _caller(type) {
		setLoading(true)
		new ApiService({
			url: `/api/v1/master-data/${type}`,
			config: {
				params: {
					...params
				}
			}
		})
			.get()
			.then((response) => {
				response.data = response.data.map((item) => ({
					...item,
					schedule_time: moment(item?.schedule_time, 'HH:mm').isValid() ? moment(item?.schedule_time, 'HH:mm') : item?.schedule_time
				}))
				setData([...response?.data])
				setPagination({
					...pagination,
					...response?.pagination,
					...response?.paginate
				})
				setLoading(false)
			})
			.catch((err) => {
				setData([])
				setLoading(false)
			})
	}
	
	
	
	
	useEffect(()=> {
		setLoading(true)
		_caller(type)
	},[params,type])
	
	
	return (
		<React.Fragment>
			<ContainerTailwind>
				<Card title={type} extra={
					<div className={'text-right`'}>
						<ButtonPermission
							permission={['admin']}
							theme={{
								type: "disabled",
								property: {
									message: "Access Denied"
								}
							}}
							config={{
								type: "primary",
								size: "small",
								ghost:true,
								icon: <FilterOutlined/>
							}}>
							Filter
						</ButtonPermission>
					</div>
				}>
					<Table
						loading={loading}
						dataSource={data}
						columns={defaultColumns ?? []}
						pagination={{
							pageSize:params?.limit,
							current: params?.page,
							total:pagination?.total_items ?? 0,
							onChange: function (page,pageSize){
								setParams({
									...params,
									page,
									limit: pageSize
								})
							}
						}}
					/>
				</Card>
			</ContainerTailwind>
		</React.Fragment>
	);
}

export default connect(
	mapStateToProps,
)(WithoutGeneralTableDisplay);