import React, { useState } from 'react';
import { connect } from 'react-redux';
import {Avatar, Card, Col, DatePicker, Form, Input, Row, Select} from "antd";
import TypeStatusAirlinesForm from "../../../../../components/forms/type-status-airlines.form";
import AirlinesSelectForm from "../../../../../components/forms/airlines.select.form";
import CitySelectForm from "../../../../../components/forms/city-select.form";
import Utils, {findKey, SearchLikeArr} from "../../../../../../../utils";
import TypeStatusSelectForm from "../../../../../components/forms/type-status.select.form";
import moment from "moment";
import Flex from "../../../../../../../components/shared-components/Flex";

function mapStateToProps(state) {
 return {

 };
}

/**
 *
 * @param {Object} props
 * @param {Object} props.form
 * @param {Object} props.initialValue
 * @returns {JSX.Element}
 */
function DisplaySystemForm(props)  {
    let {
        form,
        initialValue
    } = props

    
    const [ airline,setAirline] = useState(null)
    


  return (
   <div>
       <Form
           form={form}
           layout={'vertical'}
           initialValue={{
               ...initialValue
           }}
       >
           <Row gutter={24}>
               <Col span={16}>
                   <Form.Item
                       initialValue={
                           initialValue?.date ? moment(initialValue?.date).isValid() ? moment(initialValue?.date) : null : null
                       }
                       hasFeedback
                       name={'date'}
                       label={'Date'}
                       rules={[{required:true,message: "Can't be empty!"}]}
                   >
                       <DatePicker
                           format={'DD/MM/YYYY'}
                           style={{width:"100%"}} placeholder={'Select Date'}/>
                   </Form.Item>
               </Col>
               <Col span={8}>
                   
                   <TypeStatusAirlinesForm
                       name={'type'}
                       label={'Type'}
                       keyValue={['data','identifier']}
                       keyDisplay={['data','name']}
                       select={{
                           showSearch:true,
                           placeholder:"Select your type"
                       }}
                       mapping={function({data}){
                           let ARR = SearchLikeArr(data,['data','name'],'ARR')
                           let DEPA = SearchLikeArr(data,['data','name'],'DEPA')
                           return (
                             [
                               ...ARR,
                               ...DEPA
                             ].map((item,index)=> (
                               <Select.Option value={item?.identifier} key={item?.id}>{findKey(item,['data','name']) ?? "undefined"}</Select.Option>
                             ))
                           )
                       }}
                       initialValue={
                        initialValue?.type ?? null
                       }
                       rules={[
                           {
                               required:true,
                               message: "Can't be empty!"
                           }
                       ]}
                   />
               </Col>
               <Col span={12}>
                   <AirlinesSelectForm
                       name={'airline_id'}
                       label={'Airlines'}
                       keyValue={['id']}
                       keyDisplay={['data','name']}
                       select={{
                           onChange: function(val,args) {
                               setAirline(args)
                               form.setFieldsValue({
                                   airline_id: val
                               })
                           },
                           placeholder:"Select Airlines"
                       }}
                       initialValue={
                           initialValue?.airline_id ?? null
                       }
                       rules={[
                           {
                               required:true,
                               message: "Can't be empty!"
                           }
                       ]}
                   >
                       {function({item,index}){
                           
                           return (
                             <Select.Option value={item?.id} key={`airlines-select-${index}`} {...item}>
                                 <Flex alignItems={'center'} className={'h-full mk-gap-2'}>
                                     <Avatar src={[process.env.REACT_APP_API_MASTER,item?.data?.image?.dark].join('')} size={30}/>
                                     <span>{[findKey(item,['data','name']),`(${findKey(item,['data','code'])})` ?? ""].join(" ")}</span>
                                 </Flex>
                             </Select.Option>
                           )
                       }}
                   </AirlinesSelectForm>
               </Col>
               <Col span={12}>
                   <Form.Item
                       initialValue={
                           initialValue?.flight_no ?? null
                       }
                       
                       name={'flight_no'}
                       label={'Flight Number'}
                       rules={[
                         {required:true,message:"Can't be empty"},
                           ({ getFieldValue }) => ({
                               validator(rule, value) {
                                   if (getFieldValue('airline_id')) {
                                       return Promise.resolve();
                                   }
                                   return Promise.reject('please select the airline first!');
                               },
                           })
                       ]}>
                       <Input
                         addonBefore={airline ? airline?.data?.code ?? "-":'-'}
                           placeholder={'Enter a flight number | e.g 100'}
                       />
                   </Form.Item>
               </Col>
               <Col span={12}>
                   <CitySelectForm
                       name={'origin_id'}
                       label={'From / To'}
                       keyValue={['id']}
                       displayValue={['data','name']}
                       initialValue={
                           initialValue?.origin_id ?? null
                       }
                       select={{
                           placeholder:"Select city from / to"
                       }}
                       rules={[
                           {
                               required: true,
                               message: "Can't be empty!"
                           }
                       ]}
                   />
               </Col>
               <Col span={6}>
                   <Form.Item
                       hasFeedback
                       name={'schedule_time'}
                       label={'Schedule'}
                       rules={[{required:true,message: "Can't be empty!"}]}
                       initialValue={
                           initialValue?.schedule_time ? moment(initialValue?.schedule_time,'HH:mm').isValid() ? moment(initialValue?.schedule_time,'HH:mm'): null: null
                       }
                   >
                       <DatePicker.TimePicker
                           format={'HH:mm'}
                           disabledMinutes={()=> {
                               return Utils.disableMinutes([0],60);
                           }}
                           style={{width:"100%"}} placeholder={'Select schedule time'}/>
                   </Form.Item>
               </Col>
               <Col span={6}>
                   <Form.Item hasFeedback name={'actual_time'} label={'Actual'} initialValue={
                       initialValue?.actual_time ? moment(initialValue?.actual_time,'HH:mm').isValid() ? moment(initialValue?.actual_time,'HH:mm'): null: null
                   }>
                       <DatePicker.TimePicker
                           format={'HH:mm'}
                           disabledMinutes={()=> {
                               return Utils.disableMinutes([0],60);
                           }}
                           style={{width:"100%"}}
                           placeholder={'Select Actual Time'}/>
                   </Form.Item>
               </Col>
               <Col span={12}>
                   <TypeStatusSelectForm
                       name={'status'}
                       label={'Status'}
                       initialValue={initialValue?.status ?? null}
                       select={{
                           placeholder:"Select your Statuse"
                       }}
                       rules={[
                           {
                               required: true,
                               message: "Can't be empty!"
                           }
                       ]}
                   />
               </Col>
               <Col xs={24} lg={24}>
                   <Form.Item
                       initialValue={initialValue?.remarks ?? null }
                       hasFeedback name={'remarks'} label={'Remarks'} rules={[{required:false,message: "Can't be empty!"}]}>
                       <Input.TextArea
                           allowClear
                           placeholder={'input your remarks (optional)'}
                           showCount={true}
                           autoSize={{ minRows: 4, maxRows: 6 }}
                           size={"small"}
                       />
                   </Form.Item>
               </Col>
           </Row>
           {props?.children}
       </Form>
   </div>
  );
}

export default connect(
 mapStateToProps,
)(DisplaySystemForm);